.container {
    position: relative;
    display: flex;
    flex-direction: column;
    background: white;
    width: 80%;
    height: auto;
    margin: 1rem 1rem 1rem 1rem;
    border: 2px solid black;
    border-radius: 5px;
}
.heading {
    margin: 1rem 1rem 1rem 1rem;
    text-align: center;
}
.dropdown_label {
    color: black;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    height: 3rem;
    width: 90%;
    margin: 1rem 0rem 0rem 1rem;
}
.dropdown_labelB {
    color: black;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    height: 4rem;
    width: 90%;
    margin: 1rem 0rem 1rem 1rem;
}
.dropdown_select {
    display: flex;
    flex-direction: column;
    height: 2rem;
    width: 90%;
    margin: 0.5rem 0.5rem 0.5rem 0rem;
    
}
.checkbox_label {
    color: black;
    font-weight: bold;
    display: flex;
    flex-direction: row;
    height: 3rem;
    width: 90%;
    margin: 1rem 0rem 1rem 1rem;

}
.checkbox_input {
    display: flex;
    flex-direction: row;
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 1rem;

}
.dropdown_childcare {
    margin-right: 1.5rem;
}
.childcare_label {
    display: flex;
    flex-direction: column;
    font-weight: bold;
    margin: 1rem 1rem 1rem 1rem;
}
.childcare_label_disabled {
    color: rgb(168, 166, 166);
    display: flex;
    flex-direction: column;
    font-weight: bold;
    margin: 1rem 1rem 1rem 1rem;
}

.input_childcare {
    display: flex;
    margin-top: 0.5rem;
    margin-right: 1rem;
}
.warning_container {
    display: flex;
    color: red;
    font-weight: bold;
    margin: 0.5rem 1rem 4rem 1rem;
    height: 2rem;
    width: 90%;

}